import React, { useState , useEffect, useRef } from "react";
import { AnimatePresence,motion,useAnimation, useInView } from "framer-motion";
import {Leaf1,Leaf2,about} from "../assets";
import { ProjectsData } from "../utils/helper";
import { FaGithub } from "react-icons/fa6";

const Projects = () => {
  const ref= useRef(null)
  const isInView = useInView(ref,{once:true})
  const mainControls=useAnimation()
  useEffect(()=>{
    if(isInView){
      mainControls.start("visible")
    }
  },[isInView])
  return(
    <section id="projects" className="flex items-center justify-center flex-col gap-12 relative my-16">

    {/* title */}

    <div ref={ref} className="w-full flex justify-center items-center py-24">
       <motion.div 
        variants={{
          hidden:{opacity:0,y:75},
          visible:{opacity:1,y:0},
        }}
        initial="hidden"
        animate={mainControls}
        transition={{delay:0.6}}
        className="flex justify-around items-center w-40">
          <h1 className="font-black text-[35px] bg-clip-text text-primary">PROJECTS</h1>
        </motion.div>

    </div>

    {/* main content */}
    <div className=" grid grid-cols-1 lg:grid-cols-3 gap-4 w-full">
      <AnimatePresence>
        {ProjectsData && ProjectsData.map((project,index)=>(
          <ProjectCard key={project.id} project={project}/>
        ))}
      </AnimatePresence>
     
    </div>
  </section>
  )
};
  const ProjectCard = ({project}) => {
    const [isHoverred ,setIsHoverred ] = useState(false)
    return(
      <>
        <motion.div key={project.id} className="overflow-hidden cursor-pointer relative rounded-md"
        onMouseEnter={() => setIsHoverred(true)}
        onMouseLeave={() => setIsHoverred(false)}>

          <motion.img whileHover={{scale:1.1}} className="w-full h-full object-contain rounded-lg" src={project.imgSrc}/>

         {isHoverred && (
           <motion.div className="absolute inset-0 backdrop-blur-md bg-[rgba(0,0,0,0.6)] flex items-center justify-center flex-col gap-2">
           <p className="text-xl text-primary">{project?.name}</p>
           <p className="text-xl text-white">{project?.user}</p>
           <p className="text-xl text-white">{project?.pass}</p>
           <a href={project?.gitURL} className="">
             <FaGithub className="text-3xl text-white hover:text-primary"/>
           </a>
           </motion.div>
         )}

        </motion.div>
      </>
   
    )
  }


export default Projects;
